import React, { useState } from 'react';
import kingsley from '../../image/kingsley.png'
import Emmanuel from '../../image/Emmanuel.jpeg'
import innocent from '../../image/innocent.jpg'
import franco from '../../image/Francis.png'
import obinna from '../../image/obinna.png'
import ugo from '../../image/ugo1.png'
import styled from 'styled-components'
import '../../../CSS/Team.scss'



const teamMembers = [
  {
    name: 'Kingsley Agoha',
    position: 'Chairman',
    image: kingsley,
    bio: 'Kingsley is PHD Research and the vice-chairman of ICANWI. Has over 5 years of experience in the leadership and management. Passionate about creating innovative framework that solve real-world problems.'
  },
  {
    name: 'Ugo Nwankwo',
    position: 'Vc Chairman',
    image: ugo,
    bio: 'Emily Wilson is our customer support specialist, and she is dedicated to providing the best possible support to our customers.'
  },
  {
    name: 'Francis Udoku',
    position: 'Secretary & social Media Personnel',
    image: franco,
    bio: 'Samantha Lee is our lead designer, and she has a keen eye for detail and a passion for creating beautiful and intuitive user interfaces.'
  },
  {
    name: 'Innocent Ike',
    position: 'Finacial Secretary',
    image: innocent,
    bio: 'Jane Smith is the CTO of our company, and she has a background in computer science and software engineering. She is responsible for leading our development team and ensuring that our products are of the highest quality.'
  },
  {
    name: 'Obinna Okeagu',
    position: 'Treasurer',
    image: obinna,
    bio: 'Bob Johnson is our Marketing Director, and he has years of experience in digital marketing and branding. He is responsible for promoting our products and services to the world.'
  },

  {
    name: 'Emmanuel Okezie',
    position: 'Provost',
    image:Emmanuel ,
    bio: 'Jack Thompson is one of our senior developers, and he has years of experience in building scalable and performant web applications.'
  },

];

const Team = () => {
  return (

        <div className="meet-our-team">
          <h1><span>Meet</span> Our Executive Committe</h1>
           <h6 className='paragraph'>A dedicated group of experts with 
           complementary skills .
           </h6>
          <div className="grid">
            {teamMembers.map((member, index) => (
              <div className="grid-item" key={index}>
                <div className="image-container">
                  <img
                    src={member.image}
                    alt={member.name}
                  />
                  <div className="team-overlay">
                    <a href="/MembersDetails">
                    <i class="bi bi-search"></i>
                    </a>
                  </div>
                </div>
                <div className="text-container">
                  <h2>{member.name}</h2>
                  <p>{member.position}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      );
};

export default Team;
